
<template>
  <div class="flex flex-row">
    <label>PH</label>
    <SelectButton
      v-model="ph"
      :options="ph_options"
      optionLabel="name"
      aria-labelledby="multiple"
    />
  </div>
  <div class="flex flex-row">
    <label>PaCo2 (mmHg)</label>
    <SelectButton
      v-model="paco2"
      :options="paco2_options"
      optionLabel="name"
      aria-labelledby="multiple"
    />
  </div>
  <div class="flex flex-row">
    <label>BE (mmol/L)</label>
    <SelectButton
      v-model="be"
      :options="be_options"
      optionLabel="name"
      aria-labelledby="multiple"
    />
  </div>

  <!-- Result -->
  <Divider />

  <h2>结果：{{ calculate() }}</h2>
</template>

<script>
import SelectButton from "primevue/selectbutton";
import Divider from "primevue/divider";

export default {
  name: "App",
  components: {
    SelectButton,
    Divider,
  },
  data() {
    return {
      result: null,
      ph: null,
      paco2: null,
      be: null,
      ph_options: [
        { name: "<7.35", value: 1 },
        { name: "7.35~7.45", value: 2 },
        { name: ">7.45", value: 3 },
      ],
      paco2_options: [
        { name: "<35", value: 1 },
        { name: "35~45", value: 2 },
        { name: ">45", value: 3 },
      ],
      be_options: [
        { name: "<-3", value: 1 },
        { name: "-3 ~ +3", value: 2 },
        { name: ">3", value: 3 },
      ],
    };
  },
  methods: {
    calculate() {
      let ans1 = "无结果"
      let ans2 = "无结果"
      if (this.ph == null || this.paco2 == null || this.be == null) {
        return "请选择所有选项";
      }
      if (this.ph.value == 1) {
        ans1 = "酸中毒"
        if (this.paco2.value == 3) {
          if (this.be.value == 2) ans2 = "呼酸"
          if (this.be.value == 3) ans2 = "呼酸肾代偿"
          if (this.be.value == 1) ans2 = "呼酸合并代酸"
        }
        else if (this.paco2.value == 2 && this.be.value == 1) {
          ans2 = "代酸"
        }
        else if (this.paco2.value == 1 && this.be.value == 1) {
          ans2 = "代酸呼吸代偿"
        }
      }
      else if (this.ph.value == 3) {
        ans1 = "碱中毒"
        if (this.paco2.value == 1) {
          if (this.be.value == 2) ans2 = "呼碱"
          if (this.be.value == 1) ans2 = "呼碱肾代偿"
          if (this.be.value == 3) ans2 = "呼碱合并代碱"
        }
        else if (this.paco2.value == 2 && this.be.value == 3) {
          ans2 = "代碱"
        }
        else if (this.paco2.value == 3 && this.be.value == 3) {
          ans2 = "代碱呼吸代偿"
        }
      }
      else if (this.ph.value == 2) {
        ans1 = "pH值正常"
        if (this.paco2.value == 3 && this.be.value == 3) ans2 = "呼酸合并代碱"
        if (this.paco2.value == 1 && this.be.value == 1) ans2 = "呼碱合并代酸"
        if (this.paco2.value == 2 && this.be.value == 2) ans2 = "正常"
      }

      return ans1 + " | " + ans2;
    },
  },
};
</script>

